<template>
    <div>
        <div class="app">
            <div class="bg"><img src="@/static/images/rule/bg_guize_image@2x.png" alt=""></div>
            <div class="content">
                <div class="pr">
                    <div class="fisrtBox">
                        <i>01</i>
                        <p class="tit">自动滑落制度</p>
                        <div class="text">
                            <p>假设您已经推了A、B和C，那么您的下级三人已满</p>
                            <p>然后您再直推第四个人D入局时，D将从左到右、从上到下滑落到1/2/3/4/5/6/7/8/9的位置，哪个位置空缺就滑落到哪个，扶持了您的下级。</p>
                            <img src="@/static/images/rule/chatu01@2x.png" class="img1" alt="">
                        </div>
                    </div>
                </div>
                <!--<img class="xiu" src="@/static/images/rule/huan_iamge@2x.png" alt="">
                <img class="xius" src="@/static/images/rule/huan_iamge@2x.png" alt="">-->
                <div class="pr" style="margin-top: .19rem;">
                    <div class="twoBox">
                        <i>02</i>
                        <p class="tit">无限直推制度</p>
                        <div class="text">
                            <div class="decorate">
                                <p>举例来说</p>
                                <span></span>
                            </div>
                            <div class="dec">
                                <p>接上面的例子：假设D挂在了A的1位置，而D升级一级的钱是交给您的（不会交给A），也就是说，虽然是一推三制度，但是理论上每个人有无限的直推奖，您可以推无数人的直推，虽然这些直推是挂在您的下级下面，但这些直推奖都是直接交给您的，没有交给您的下级。
                                    所以系统鼓励直推，只要是输入您的邀请码直推的，直推奖400元（小额版10元）全部是交给您的，但位置是挂在下级下面的。</p>
                            </div>
                        </div>
                    </div>
                </div>
                <!--<img class="xiu1" src="@/static/images/rule/huan_iamge@2x.png" alt="">
                <img class="xius1" src="@/static/images/rule/huan_iamge@2x.png" alt="">-->
                <div class="pr">
                    <div class="fisrtBox">
                        <i>03</i>
                        <p class="tit">间接扶持下级 </p>
                        <div class="text">
                            <p>当自动滑落在A下面的用户超过2层时，A就可以收到下2级用户升级2级的资金。</p>
                            <p>当自动滑落在A下面的用户超过3层时，A就可以收到下3级用户升级3级的资金。</p>
                            <p>当自动滑落在A下面的用户超过4层时，A就可以收到下4级用户升级4级的资金。</p>
                            <p>以此类推</p>
                            <img src="@/static/images/rule/chatugg.png" class="img2" alt="">
                        </div>
                    </div>
                </div>
                <!--<img class="xiu2" src="@/static/images/rule/huan_iamge@2x.png" alt="">
                <img class="xius2" src="@/static/images/rule/huan_iamge@2x.png" alt="">-->
                <div class="pr" style="margin-top: .19rem;">
                    <div class="twoBox">
                        <i>04</i>
                        <p class="tit">等级不够将流失收益</p>
                        <div class="text">
                            <p class="wenLi">1、自己的等级是几级，就可以收到下几级的升级资金。</p>
                            <div class="decorate">
                                <p>举例来说</p>
                                <span></span>
                            </div>
                            <div class="dec">
                                <p>您的等级是1级，您可以收到下1级的升级资金。</p>
                                <p>您的等级是2级，您可以收到下2级的升级资金。</p>
                                <p>以此类推</p>
                            </div>
                            <div class="imgBox"><img src="@/static/images/rule/chatu03@2x.png" class="img3" alt=""></div>
                            <p class="wenLi">2、下级的等级超过了自己，那么下级的升级费用您就收不到了。</p>
                            <div class="decorate">
                                <p>举例来说</p>
                                <span></span>
                            </div>
                            <div class="dec">
                                <p>您的等级是2级，您就收不到下级升级3级的钱（及3级以上的钱）</p>
                                <p>您的等级是3级，您就收不到下级升级4级（及4级以上的钱）</p>
                            </div>
                            <div class="decorate">
                                <p>例如</p>
                                <span></span>
                            </div>
                            <div class="dec">
                                <p>您的等级为1级，您推荐了A和B，收到了A和B升级1级的资金，而您没有及时升级2级，A和B推荐的伙伴升级为2级（及升2级以上的钱）的资金您就收不到了，这部分收益将流失。</p>
                            </div>
                            <div class="imgBox"><img src="@/static/images/rule/chatu04@2x.png" class="img3" alt=""></div>
                            <p class="foot">所以您需要尽快升级到高级别，这样下级缴纳的升级资金才不会流失。</p>
                        </div>
                    </div>
                </div>
                <!--<img class="xiu3" src="@/static/images/rule/huan_iamge@2x.png" alt="">
                <img class="xius3" src="@/static/images/rule/huan_iamge@2x.png" alt="">-->
                <div class="pr" style="margin-top: .19rem;">
                    <div class="twoBox">
                        <i>05</i>
                        <p class="tit">升级公式</p>
                        <div class="text">
                            <p class="wenLi">请及时关注您的下级情况，保持您的等级高于下级层数，否则会错失下级收益</p>
                            <div class="decorate">
                                <p>升级公式</p>
                                <span></span>
                            </div>
                            <div class="dec">
                                <p>您应该升级的等级=您的下级层数+2</p>
                            </div>
                            <div class="decorate">
                                <p>例如</p>
                                <span></span>
                            </div>
                            <div class="dec">
                                <p>假设您下级是1层，建议您的级别至少在3级（您的下级层数1+2=3），最好在3级以上，这样才不会错失下级升级收益。</p>
                                <p>假设您下级是2层，建议您的级别至少在4级（您的下级层数2+2=4），最好在4级以上，这样才不会错失下级升级收益。</p>
                                <p>以此类推</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <tabbar></tabbar>
    </div>

</template>

<script>
    import tabbar from '@/components/tabbar';
    export default {
        components:{
            tabbar
        },
        name: "Rule"
    }
</script>

<style scoped>
    @import "../../static/css/rule.css";
</style>



